<template>
    <div class="fb-page">
        <div class="fb-title">
            <el-tag type="warning">
                <i class="el-icon-info"></i>
                ~...
            </el-tag>     
        </div>   
        <el-form ref="form" :model="form" :rules="rules" label-width="100px">
            <el-form-item label="" prop="title">
                <el-input v-model="form.title" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="form.ftype">
                    <el-option label="BUG" value="shanghai"></el-option>
                    <el-option label="" value="beijing"></el-option>
                    <el-option label="" value="beijing"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="contact">
                <el-input v-model="form.contact" placeholder="(QQ//)"></el-input>
            </el-form-item>
            <el-form-item label="" prop="desc">
                <el-input 
                    type="textarea" 
                    v-model="form.desc" 
                    :autosize="{ minRows: 16, maxRows: 16}"
                    placeholder="..."
                ></el-input>
            </el-form-item>
            <el-form-item style="float: right">
                <el-button @click="onClear" round></el-button>
                <el-button type="primary" @click="onSubmit" round></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            form: {
                title: '',
                ftype: 'BUG',
                contact: '',
                desc: ''
            },
            rules: {
                title: [{ required: true, message: '', trigger: 'blur' }],
                contact: [{ required: true, message: '', trigger: 'blur' }],
                desc: [{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    methods: {
        onSubmit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.$message({
                        type: 'success',
                        message: '',
                        center: true,
                        duration: 2500
                    });
                }
            });
        },
        onClear() {
            this.$msgbox.confirm(', ?', '', {
                                confirmButtonText: '',
                                cancelButtonText: '',
                                type: 'warning',
                                callback: (action,instance) => {
                                    if (action == 'confirm') {
                                        this.$refs.form.resetFields();
                                    }
                                }});
        }
    }    
}
</script>

<style scoped>

.fb-page {
    padding-top: 2%;
    padding-left: 4%;
    padding-right: 4%;
}

.fb-title {
    text-align: center;
    margin-bottom: 24px;
}

</style>
